import React from "react";
import "./Contact.scss";

import GuildedIcon from "../Icons/Guilded.png";
import DiscordIcon from "../Icons/Discord.png";
import EmailIcon from "../Icons/Email.png";
import GithubIcon from "../Icons/GitHub.png";

import {copyToClipboard} from "../Components/Modals";
import App from "../App";

class ContactItem extends React.Component {
    render() {
        const { icon, children, invert } = this.props;
        
        return (
            <div className="ContactItem">
                <img className="ContactIcon" src={icon} alt="guilded_icon" style={{ filter: invert ? "invert(100%)" : null }}/>
                <span className="ContactMain">
                    { children }
                </span>
            </div>
        );
    }
}

export default class Contact extends React.Component {
    render() {
        const sep = App.isMobile ? (<br/>) : " - ";
        
        return (
            <div className="ContactPage">
                <ContactItem icon={GuildedIcon}>
                    <b>Guilded Server</b>{sep}<a href="https://guilded.gg/elemental-reign">https://guilded.gg/elemental-reign</a>
                </ContactItem>
                <ContactItem icon={GuildedIcon}>
                    <b>My Guilded Profile</b>{sep}<a href="https://guilded.gg/metalloriff">https://guilded.gg/metalloriff</a>
                </ContactItem>
                <ContactItem icon={DiscordIcon}>
                    <b>My Discord Profile</b>{sep}<a onClick={() => copyToClipboard("Metalloriff 💕🌈#2891")}>Metalloriff 💕🌈#2891</a>
                </ContactItem>
                <ContactItem icon={EmailIcon}>
                    <b>My Email</b>{sep}<a onClick={() => copyToClipboard("metalloriff@gmail.com")}>metalloriff@gmail.com</a>
                </ContactItem>
                <ContactItem icon={GithubIcon} invert>
                    <b>My GitHub</b>{sep}<a href="https://github.com/Metalloriff">https://github.com/Metalloriff</a>
                </ContactItem>
            </div>
        );
    }
}