import React from "react";
import { useMediaQuery } from "react-responsive";
import { HashRouter, Switch, Route } from "react-router-dom";
import "./App.scss";
import { closeModal, copyToClipboard, Modals, openModal } from "./Components/Modals";
import Icon from "./Icons/Icon.png";

import HomePage from "./Pages/Home";
import ContactPage from "./Pages/Contact";
import SpellBookPage from "./Pages/SpellBook";

import Toasts from "./Components/Toasts";

class HeaderButton extends React.Component {
	componentDidMount() {
		window.addEventListener("popstate", () => this.forceUpdate());
	}

	render() {
		const { label, path, style } = this.props;
		const active = window.location.hash === path;
		
		return (
			<a className={"HeaderButton " + (active ? "Active" : "")} style={style} href={path}>
				{ label }
			</a>
		)
	}
}

class Header extends React.Component {
	state = { sidebarOpen: false };

	render() {
		const { isMobile } = this.props;

		return (
			<header className={"Header " + (this.state.sidebarOpen ? "Active" : "Inactive")}>
				<div className="HeaderButtons">
					<a className="MainIcon" style={{ backgroundImage: `url("${Icon}")` }} href="#/"/>
					
					<HeaderButton label="Home" path="#/"/>
					<HeaderButton label="Contact" path="#/contact"/>
					<HeaderButton label="Spell Book" path="#/spells"/>
					<HeaderButton label="Join The Guilded Server" path="https://www.guilded.gg/elemental-reign" style={{ float: "right" }}/>
				</div>
			</header>
		);
	}
}

export default function App() {
	const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
	App.isMobile = isMobile;

	App.showNotice = function(text) {
		openModal(
			<div className="ClipboardModal" onClick={() => closeModal()}>
				<span>
					{ text }
				</span>
			</div>
		);
	};

	return (
		<div className={"App " + (isMobile ? "Mobile" : "Desktop")}>
			<div>
				<Header isMobile={isMobile}/>

				<div className="Main">
					<HashRouter basename="/">
						<Switch>
							<Route exact path="/" component={HomePage}/>
							<Route path="/contact" component={ContactPage}/>
							<Route path="/spells" component={SpellBookPage}/>
						</Switch>
					</HashRouter>
				</div>

				<footer className="Footer">
					<span>
						Copyright © 2021 Shrimp Plug Games | 
						Site by <a href="https://metalloriff.github.io/toms-discord-stuff/#/contact">Metalloriff</a>
					</span>
				</footer>
			</div>

			<Modals/>
			<Toasts/>
		</div>
	);
}