import React from "react";
import "./SpellBook.scss";

function unityFormat(description) {
    return { __html: " - " + description
        .replace(/<color=#([A-Za-z0-9]*)>(.+?)<\/color>/gm, `<span style="color:#$1">$2</span>`)
        .split("\n").join("<br/>") };
}

class CategoryItem extends React.Component {
    state = { collapsed: true };
    
    render() {
        const { name, description, hidden, icon, abilities, children, level = "" } = this.props;
        const { collapsed } = this.state;
        
        return (
            <div className="Item">
                <div>
                    <div className="Icon" style={{ backgroundImage: `url("${icon}")` }}/>
                    <div className="Name" onClick={() => this.setState({ collapsed: !collapsed })}>
                        { name }
                        <span className="Description" dangerouslySetInnerHTML={ unityFormat(description) }/>
                        <span className="Level">{ level }</span>
                    </div>
                </div>
                
                { collapsed ? null : (children && React.Children.count(children) ? children : (
                    <div className="Item">
                        No contents...
                    </div>
                )) }
            </div>
        );
    }
}

class AbilityItem extends React.Component {
    renderProp(key, value) {
        return (
          <div className="AbilityProp" key={key}>
              <span className="Key">{ key.replace(/([A-Z]+)/g, " $1") }</span>
              <span className="Value" dangerouslySetInnerHTML={ unityFormat(value) }/>
          </div>  
        );
    }
    
    render() {
        const props = ["type", "costType"];
        
        return (
            <CategoryItem {...this.props}>
                <div className="Item">
                    { props.map(p => this.renderProp(p, this.props[p])) }
                    { this.props.props.map(p => this.renderProp(p.label, p.content)) }
                </div>
            </CategoryItem>
        );
    }
}

export default class SpellBook extends React.Component {
    state = { data: null, showHidden: false };
    
    render() {
        const { data, showHidden } = this.state;
        
        if (!data)
            fetch("https://www.dropbox.com/s/jz9yzza76hwr3i8/SpellBook.json?raw=1")
                .then(res => res.json())
                .then(data => this.setState({ data }));
        
        const sortByLevel = (a, b) => {
            a = parseInt(a.level.replace(/[^0-9]*/gmi, ""));
            b = parseInt(b.level.replace(/[^0-9]*/gmi, ""));
            
            return a - b;
        };
        
        return data ? (
            <div className="SpellBookPage">
                <div className="CheckboxContainer" onClick={() => this.setState({ showHidden: !showHidden })}>
                    <span className="Label">Show hidden</span>
                    <div className="Checkbox">
                        <div className={"Ticked " + (showHidden ? "Active" : "")}/>
                    </div>
                </div>
                
                { Object.values(data).filter(c => !c.hidden || showHidden).map(d => (
                    <CategoryItem key={d.name} {...d}>
                        <div>
                            <CategoryItem name="Standard Abilities" description="Standard, slot-able spells and abilities."
                                          icon="https://flaticons.net/images/icons/application/thumbs/arrowhead-down-l.png">
                                { d.abilities.filter(a => ["Spell", "Ability"].includes(a.type) && (!a.hidden || showHidden)).sort(sortByLevel).map(a =>
                                    <AbilityItem key={a.name} {...a}/>) }
                            </CategoryItem>
                            <CategoryItem name="Primary Abilities" description="Primary abilities, commonly cost-free."
                                          icon="https://flaticons.net/images/icons/application/thumbs/arrowhead-left-01-l.png">
                                { d.abilities.filter(a => a.type === "Primary" && (!a.hidden || showHidden)).sort(sortByLevel).map(a => 
                                    <AbilityItem key={a.name} {...a}/>) }
                            </CategoryItem>
                            <CategoryItem name="Secondary Abilities" description="Standard, slot-able spells and abilities."
                                          icon="https://flaticons.net/images/icons/application/thumbs/arrowhead-right-01-l.png">
                                { d.abilities.filter(a => a.type === "Secondary" && (!a.hidden || showHidden)).sort(sortByLevel).map(a =>
                                    <AbilityItem key={a.name} {...a}/>) }
                            </CategoryItem>
                            <CategoryItem name="Ultimate Abilities" description="Ultimate abilities, charged by performing combos, powerful and cost ultimate."
                                          icon="https://flaticons.net/images/icons/application/thumbs/arrowhead-up-l.png">
                                { d.abilities.filter(a => a.type === "Ultimate" && (!a.hidden || showHidden)).sort(sortByLevel).map(a =>
                                    <AbilityItem key={a.name} {...a}/>) }
                            </CategoryItem>
                        </div>
                    </CategoryItem>
                )) }
            </div>
        ) : (
            <div className="SpellBookPage">
                <div className="SpinnerBG">
                    <div className="Spinner"/>
                    <div className="SpinnerLabel">Fetching data...</div>
                </div>
            </div>
        );
    }
}