import React, {createRef} from "react";
import "./Home.scss";

import App from "../App";

import Multiplayer from "../Icons/Multiplayer.png";
import PvEIcon from "../Icons/PvE.png";
import PvPIcon from "../Icons/PvP.png";
import BuildingIcon from "../Icons/Building.png";
import CraftingIcon from "../Icons/Crafting.png";
import CommandsIcon from "../Icons/Commands.png";
import ServerControlIcon from "../Icons/ServerControl.png";
import MoreIcon from "../Icons/More.png";

import BackgroundImages from "../BackgroundImages";

let contentTooltip;

class ContentItem extends React.Component {
	handleMouseEnter = e => {
		contentTooltip.setState({ text: this.props.children });
	}
	
	handleMouseExit = e => {
		contentTooltip.setState({ text: contentTooltip.placeholder() });
	}
	
	render() {
		const { icon } = this.props;
		
		return (
			<div className="ContentItem" style={{ backgroundImage: `url("${icon}")` }}
				 onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseExit}/>
		);
	}
}

class ContentTooltip extends React.Component {
	state = { text: this.placeholder() };
	
	placeholder() {
		return (
			<div style={{ opacity: 0.5 }}>
				<b>Hint</b> - {( App.isMobile ? "Tap on" : "Hover over" )} the icons above for information about the game's content.
			</div>
		);
	}

	componentDidMount() {
		contentTooltip = this;
	}

	render() {
		const { text } = this.state;
		
		return (
			<div className="ContentTooltip">
				{ text }
			</div>
		);
	}
}

export default class extends React.Component {
	async componentDidMount() {
		const image1 = document.getElementById("image1");
		const image2 = document.getElementById("image2");
		let idx = 0;
		
		image1.style.backgroundImage = `url("${BackgroundImages[idx]}")`;
		
		let activeImage = image2;
		let inactiveImage = image1;
		
		const sleep = t => new Promise(r => setTimeout(r, t * 1000));
		
		while (this) {
			idx++;
			if (idx >= BackgroundImages.length)
				idx = 0;

			activeImage = activeImage === image1 ? image2 : image1;
			inactiveImage = activeImage === image1 ? image2 : image1;

			activeImage.style.animation = "active-image linear";

			inactiveImage.classList.remove("Active");
			activeImage.classList.add("Active");

			await sleep(2);

			inactiveImage.style.animation = null;
			inactiveImage.style.backgroundImage = `url("${BackgroundImages[idx]}")`;
			
			await sleep(5);
		}
	}

	render() {
		return (
			<div className="MainPage">
				<div id="image1" className="Background"/>
				<div id="image2" className="Background"/>
				
				<div className="MainBody">
					<b>Elemental Reign</b><br/>
					is a survival-sandbox RPG based around magical spells and abilities powered by classes' corresponding planets.<br/>
					<div className="ContentList">
						<ContentItem icon={Multiplayer}>
							<b>Fully supported multiplayer</b> - Anything you can do alone, you can do with friends.
						</ContentItem>
						<ContentItem icon={PvEIcon}>
							<b>PvE combat</b> - Dungeons, random encounters, etc.
						</ContentItem>
						<ContentItem icon={PvPIcon}>
							<b>PvP combat</b> - Log on with friends or strangers, and fight to the death!
						</ContentItem>
						<ContentItem icon={BuildingIcon}>
							<b>House building and decorating</b> - Multi-layer house building with interactable and customizable furnishings.
						</ContentItem>
						<ContentItem icon={CraftingIcon}>
							<b>Resource gathering and crafting</b> - Simple and easy crafting UI, increase your resource gather speed the faster you can attack.
						</ContentItem>
						<ContentItem icon={CommandsIcon}>
							<b>Fun and chaotic commands</b> - Hundreds of console commands, all listed and described in an organizable list.
						</ContentItem>
						<ContentItem icon={ServerControlIcon}>
							<b>Extensive server control</b> - Control exactly what your players can do with the detailed server settings.
						</ContentItem>
						<ContentItem icon={MoreIcon}>
							<b>Much more to come!</b> - ER has been in dev for nearly 2 years now, and is being actively developed.
						</ContentItem>
						
						<ContentTooltip/>
					</div>
				</div>
			</div>
		);
	}
}